.headerContainer {
  display: flex;
  background-color: white;
  margin-top: 20px;
  margin-left: 150px;
  margin-right: 150px;
  justify-content: space-evenly;
}

.emptyContainer {
  width: 33%;
}

.headerlogoContainer {
  width: 33%;
}

.headerLogo {
  max-width: 20%;
}

.socialContainer {
  width: 33%;
  justify-content: space-evenly;
  display: flex;
}

.downloadWhitepaper {
  max-height: 50px;
  cursor: pointer;
  font-size: 20px;
  color: #c6cde8;
  font-weight: bold;
  margin-right: 10px;
}

.connectWallet {
  max-height: 50px;
  cursor: pointer;
  font-size: 20px;
  display: flex;
  cursor: pointer;
  border: 2px solid #c6cde8;
  padding: 18px;
  font-weight: bold;
  color: #c6cde8;
}

.headerButton {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 450px) {
  .headerContainer {
    display: flex;
    background-color: white;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 450px) {
  .emptyContainer {
    width: 33%;
  }
}
@media only screen and (max-width: 450px) {
  .twitterLogo {
    max-height: 20px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 450px) {
  .instagramLogo {
    max-height: 20px;
    margin-left: 30px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 950px) {
  .twitterLogo {
    max-height: 25px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 950px) {
  .instagramLogo {
    max-height: 25px;
    margin-left: 30px;
    cursor: pointer;
  }
}
@media only screen and (max-width: 450px) {
  .headerContainer {
    display: flex;
    background-color: white;
    margin-top: 20px;
    margin-left: 50px;
    margin-right: 50px;
    justify-content: space-evenly;
  }

  .downloadWhitepaper {
    max-height: 25px;
    cursor: pointer;
    font-size: 20px;
    color: #c6cde8;
    font-weight: bold;
    font-size: small;
  }

  .connectWallet {
    max-height: 25px;
    cursor: pointer;
    font-size: 20px;
    display: flex;
    cursor: pointer;
    border: 2px solid #c6cde8;
    padding: 10px;
    font-weight: bold;
    color: #c6cde8;
    font-size: small;
  }
}
