.bannerContainer {
  background-color: white;
  display: flex;
  align-items: center;
  margin-left: 150px;
  margin-right: 150px;
  justify-content: center;
}

.bannerHeader {
  max-width: 1000px;
}

.headBanner {
  font-weight: bolder;
  font-size: 60px;
  margin-top: 100px;
  color: #c6cde8;
}

.bodyBanner {
  font-weight: bold;
  font-size: 40px;
  margin-bottom: 100px;
  color: #f9c0c2;
}

.fraction {
  max-width: 280px;
  margin-bottom: 100px;
  border-radius: 1000px;
  border: 6px solid #c6cde8;
}

.discordButton {
  font-size: 22px;
  display: flex;
  cursor: pointer;
  font-family: "CaviarDreams";
  border: 2px solid #c6cde8;
  background-color: #c6cde8;
  color: white;
  font-weight: bold;
  padding: 18px;
  justify-content: center;
  width: 120px;
  margin-left: 15px;
}

.openseaButton {
  font-size: 22px;
  display: flex;
  cursor: pointer;
  font-family: "CaviarDreams";
  border: 2px solid #c6cde8;
  font-weight: bold;
  padding: 18px;
  justify-content: center;
  width: 120px;
  margin-right: 15px;
}

@media only screen and (max-width: 500px) {
  .bannerContainer {
    background-color: white;
    display: flex;
    align-items: center;
    margin-left: 150px;
    margin-right: 150px;
    justify-content: center;
  }

  .bannerHeader {
    max-width: 1000px;
  }

  .headBanner {
    font-weight: bolder;
    font-size: 50px;
    margin-top: 30px;
  }

  .bodyBanner {
    font-weight: bold;
    font-size: 25px;
    margin-bottom: 50px;
    color: #f9c0c2;
  }

  .fraction {
    max-width: 150px;
    margin-bottom: 50px;
    border-radius: 1000px;
    border: 6px solid #c6cde8;
  }

  .discordButton {
    font-size: 14px;
    display: flex;
    cursor: pointer;
    font-family: "CaviarDreams";
    border: 2px solid #c6cde8;
    background-color: #c6cde8;
    color: white;
    font-weight: bold;
    padding: 8px;
    justify-content: center;
    margin-right: 15px;
  }

  .openseaButton {
    font-size: 14px;
    display: flex;
    cursor: pointer;
    font-family: "CaviarDreams";
    border: 2px solid #c6cde8;
    font-weight: bold;
    padding: 8px;
    justify-content: center;
    color: #c6cde8;
  }

  .joinBanner {
    display: flex;
  }
}
