.Merchandise {
  max-height: 300px;
}

.comingSoon > imgs {
  max-height: 300px;
}

.merchContainer {
  margin-top: 100px;
  margin-bottom: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

@media (max-width: 700px) {
  .merchContainer {
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
}

@media only screen and (max-width: 450px) {
  .merchContainer {
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    display: none;
  }

  .Merchandise {
    max-width: 100px;
    display: none;
  }

  .comingSoon {
    max-width: 100px;
    display: none;
  }
}
